<template lang="html">
  <transition name="fade" mode="out-in">
    <template v-if="ajax_fetch_data">
      <Spin size="large"></Spin>
    </template>
    <template v-else>
      <div class="layout-content-main">
        <div class="">
          <div class="mahasiswa-list-wrap">
            <Mahasiswa-List v-for="(item,index) in mahasiswa" :key="item.nim_mahasiswa" :data_mhs="item"></Mahasiswa-List>
          </div>
        </div>
      </div>
    </template>
  </transition>
</template>

<script>
import MahasiswaList from './Mahasiswa_List.vue';
export default {
  name: "MahasiswaIndex",
  props: {

  },
  //variable
  data: function(){
    return{
      ajax_fetch_data: '',
      mahasiswa: ''
    }
  },
  computed: {

  },
  components: {
    MahasiswaList
  },
  //methods
  watch: {

  },
  methods: {
    get_mahasiswa: function(){
      this.ajax_fetch_data = true;
      this.$store.dispatch('get_all_mahasiswa').then((mahasiswa) => {
        this.mahasiswa = mahasiswa;
        this.ajax_fetch_data = false;
      });
    }
  },
  //components life cycle hook
  created: function(){
    this.get_mahasiswa();
  }
}
</script>

<style lang="css" scoped>
.layout-content-main{
  background-color: white;
}
.mahasiswa-list-wrap{
  padding: 15px;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
.hide {
  overflow: hidden;
  max-height: 0;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 0;
  margin-bottom: 0;
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}
.show {
  -moz-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -moz-transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in;
  -o-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  max-height: 1000px;
  overflow: hidden;
}
</style>



